body {
  font-family: sans-serif;
}
.no-select {
  user-select: none;
}
table td {
  width: 30px;
  height: 30px;
  background: orange;
  text-align: center;
  vertical-align: middle;
  cursor: default;
  transition: color .3s, background .3s;
  user-select: none;
}
table td.active {
  background: brown;
  color: #fff;
}
.btn-set {
  padding: 10px;
}
button {
  border: none;
  background: brown;
  color: #fff;
  display: inline-block;
  line-height: 30px;
  padding: 0 20px;
}
